<template>
    <el-container class="orgManage">
        <!-- 机构管理 -->
        <el-container style="height: 100%">
            <el-aside width="20%" >
                <!--<el-scrollbar style="height: 100%">-->
                    <div :style="{height:siteContentViewHeight -10 +'px',overflow:'auto'}">
                      <org-tree ref="orgTree" @node-click="orgTreeClick" :treeData="treeData"></org-tree>
                    </div>
                <!--</el-scrollbar>-->
            </el-aside>
            <el-main>
                <div class="search">
                    <!-- 机构管理主表 -->
                     <div>
                        <el-col :span="19"  >
                        <el-input size="small" placeholder="请输入机构名" v-model="keyWord"></el-input>
                        </el-col>
                        <el-col :span="3" style="margin-left:10px;">
                            <el-button type="primary" size="small"  @click="searchUser" >检索</el-button>
                        </el-col>
                    </div>
                    <div >
                        <el-button type="primary" size="small"  @click="addUser" >添加</el-button>
                         <el-button type="primary" size="small" @click="moveDirection('up')">上移</el-button>
                         <el-button type="primary" size="small" @click="moveDirection('down')">下移</el-button>
                        <el-button type="primary" size="small"  @click="saveSort" >保存排序</el-button>
                    </div>
                </div>
                <el-table ref="multipleTable" :data="tableData" @selection-change="selectionChangeHandle" tooltip-effect="dark" style="width: 100%" :height="siteContentViewHeight - 100 +'px'">
                    <el-table-column type="selection" width="55" align="center"></el-table-column>
                    <el-table-column width="50px" fixed type="index" :index="indexMethod" label="序号" align="center"></el-table-column>
                    <el-table-column prop="orgCode" label="机构代码" ></el-table-column>
                    <el-table-column prop="orgName" label="机构名称" ></el-table-column>
                    <el-table-column prop="orgType" label="机构类型" >
                        <template slot-scope="scope">
                            <div  v-if="scope.row.orgType=='1'">单位</div>
                            <div v-else>部门</div>
                        </template>
                    </el-table-column>
                    <el-table-column prop="orgStatus" label="机构状态" >
                        <template slot-scope="scope">
                            <el-tag size="medium" type="error" v-if="scope.row.orgStatus=='1'">禁用</el-tag>
                            <el-tag size="medium" type="success" v-else-if="scope.row.orgStatus=='0'">启用</el-tag>
                        </template>
                    </el-table-column>
                    <el-table-column fixed="right" label="操作" align="center" width="160px">
                        <template slot-scope="scope">
                            <el-button
                                size="mini"
                                plain
                                type="primary"  title="编辑"
                                @click="updateUser(scope.row, scope.$index)">编辑
                            </el-button>
                             <el-button
                                    title="删除"
                                    size="mini"
                                    plain
                                    type="danger"
                                    @click="delUser(scope.row, scope.$index)">删除
                            </el-button>
                            </template>
                        </el-table-column>
                </el-table>
                <!--分页-->
                <el-pagination
                   @size-change="handleSizeChange"
                   @current-change="handleCurrentChange"
                   :current-page="page.pageNum"
                   :page-sizes="page.pageSizes"
                   :page-size="page.pageSize"
                   layout="total, sizes, prev, pager, next, jumper"
                   :total="page.total">
                </el-pagination>
                <!-- 弹出框--机构添加 -->
                <el-dialog :title="dialogTitle" :visible.sync="orgUpdateVisable" width="40%" v-if="orgUpdateVisable" :close-on-click-modal ="false">
                    <org-update :currentRow="currentRow" :isEdit="isEdit"
                                 @updateCallback="updateCallback"></org-update>
                </el-dialog>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import {swapItems} from '@/utils';
    import orgTree from "@/components/baseComponents/orgTree.vue";
    import orgUpdate from "./orgUpdate.vue";
    export default {
        name: "orgManage",
        components: {
            orgTree,
            orgUpdate
        },
        props:['siteContentViewHeight'],
        data() {
            return {
                Height: {
                    height:(document.documentElement.clientHeight - 200)+ "px"
                },
                orgUpdateVisable: false,
                dialogTitle: '',
                treeData: [],  //机构数据
                nodeId: '',   //选中机构id
                tableData: [],  //列表数据
                currentRow: {},  //机构当前选中行
                isEdit: true,  //true 编辑
                keyWord: "",
                page: {
                    pageNum: 1,
                    pageSize: 10,
                    pageSizes: [10, 20, 30, 40, 50],
                    total: 0
                },
            }
        },
        created() {
            this.initOrgTree();
        },
        methods: {
            //加载机构树
            initOrgTree() {
                let _this = this;
                _this.$http({
                    url: _this.$http.adornUrl(_this.API.getOrganizationTree),
                    method: 'post',
                    data:_this.$http.adornData()
                 }).then(({data}) => {
                    if (data.code === 0) {
                        _this.treeData = data.data;
                        _this.orgTreeClick(_this.treeData[0]);
                       _this.nodeId = _this.treeData[0].id;
                        _this.getUserData();
                    } else {
                        _this.$message.error(res.msg);

                    }
                }).catch(function (error) {
                     _this.$message.error(error);
                })
            },
            //机构树点击事件
            orgTreeClick(data) {
                this.nodeId = data.id;
                this.getUserData();
            },
            indexMethod(index) {
                return index + 1;
            },
            //机构列表查询
            getUserData() {
                let _this = this;
                let orgId = _this.nodeId
                let keyWord = _this.keyWord;
                if (orgId === null || orgId === '') {
                    this.$message({
                        type: 'warning',
                        message: "请选择结构节点！"
				    });
                    return false;
                }else{
                    _this.$http({
                        url: _this.$http.adornUrl(_this.API.getOrganztListByB0000),
                        method: 'post',
                        data:_this.$http.adornData({
                            pageNum: _this.page.pageNum,
                            pageSize: _this.page.pageSize,
                            orgId:orgId,
                           orgName:keyWord,

                        })
                    }).then(({data}) => {
                        if (data.code === 0) {
                            _this.tableData = data.data;
                            _this.page.total = data.total;
                        }
                    }).catch(function (error) {
                    });
                }
            },
            //设置分页大小
            handleSizeChange(pageSize) {
                this.page.pageSize = pageSize;
                this.getUserData();
            },
            //当前页改变
            handleCurrentChange(pageNum) {
                this.page.pageNum = pageNum;
                this.getUserData();
            },
            //检索
            searchUser() {
                this.getUserData();
            },
            addUser() {
                if(this.nodeId){
                     this.currentRow.orgPid = this.nodeId;
                }else{
                    this.currentRow.orgPid = '0';
                }
                this.dialogTitle = '添加机构';
                this.orgUpdateVisable = true;
                this.isEdit = false;
            },
            updateUser(row) {
                this.currentRow = row;
                this.dialogTitle = '编辑机构';
                this.orgUpdateVisable = true;
                this.isEdit = true;
            },
            //机构操作回调
            updateCallback(flag) {
                if (flag) {
                    this.getUserData();
                    this.initOrgTree();
                }
                this.orgUpdateVisable = false;
            },
             selectionChangeHandle(val) {//多选
                this.tableDataSelections=val;
            },
             moveDirection(direction){ //移动
                var that=this,index;
                that.tableDataSelections.sort((prev,next)=>{
                    return that.tableData.indexOf(prev)-that.tableData.indexOf(next)
                })
                if(direction=='down') that.tableDataSelections.reverse()
                that.tableDataSelections.forEach(item=>{
                    index=that.tableData.indexOf(item); //得到右侧选中值的标识坐标
                    if(direction=='up') that.moveUp(index);
                    else if(direction=='down') that.moveDown(index)
                })
            },
            moveUp(index){//上移
                if(index==0) return;
                this.tableData=swapItems(this.tableData,index,index-1,'up');
            },
            moveDown(index){ //下移
                if(index==this.tableData.length-1) return;
                this.tableData=swapItems(this.tableData,index,index+1,'down');
            },
            saveSort(){//保存排序
                var sortArr=[];
                if(this.tableData.length<0) return;
                this.tableData.forEach((item,index)=>{
                sortArr.push({
                    orgId:item.orgId,
                    orderno:index+1
                })
                })
                this.$http({
                url: this.$http.adornUrl(this.API.orgSort),
                method: 'post',
                data: this.$http.adornData({
                    data:JSON.stringify(sortArr)
                })
                }).then(({data}) => {
                if(data && data.code===0){
                    this.$message.success('操作成功');
                    this.getUserData();
                }else{
                    this.$message.error(data.msg);
                }
                });
            },
            //删除
            delUser(row) {
                let _this =  this
                _this.$confirm('确定删除本条数据？', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    _this.$http({
                        url: _this.$http.adornUrl(_this.API.deleteOrganization),
                        method: 'post',
                        data:_this.$http.adornData({
                            orgId: row.orgId
                        })
                    }).then(({data}) => {
                            if (data.code === 0) {
                                _this.$message.success('删除成功');
                                _this.getUserData();
                                _this.initOrgTree();
                            } else {
                                _this.$message.error('删除失败');
                            }
                        })
                        .catch(function (error) {
                        });
                })
            },

        },
        watch: {
            filterText(val) {
                    this.$refs.orgTree.filter(val);
                },
                fullHeight(val) {
                    if (!this.timer) {
                    this.fullHeight = val;
                    this.timer = true;
                    let that = this;
                    setTimeout(function() {
                        that.timer = false;
                    }, 400);
                    }
            }
        },
        mounted() {
            const that = this;
            window.onresize = () => {
            return (() => {
                window.fullHeight = document.documentElement.clientHeight;
                that.fullHeight = window.fullHeight;
            })();
            };
        },
    }
</script>

<style lang="scss" scoped>
    .el-aside {
        border-right: 1px solid #e6e6e6;
        padding-top: 10px;
        height: 100vh;
    }
    .el-pagination {
        /*position: fixed;*/
        right: 20px;
        bottom: 20px;
    }
    .search{
        display: flex;
        flex-direction:row;
        justify-content : space-between;
        width: 97%;
        margin: 0 10px 10px 10px;
    }
    >>> .el-main{
      padding-bottom: 0;
    }
</style>



