<template>
    <div>
        <el-form :model="orgForm" :rules="rules" ref="orgForm" size="small">
            <el-form-item label="机构代码" :label-width="formLabelWidth" prop="orgCode">
                <el-input v-model="orgForm.orgCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="机构名称" :label-width="formLabelWidth" prop="orgName">
                <el-input v-model="orgForm.orgName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="机构类型" prop="orgType" :label-width="formLabelWidth" >
                <el-select v-model="orgForm.orgType" style="width:100%;">
                    <el-option label="单位" value="1"></el-option>
                    <el-option label="部门" value="2"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="机构状态" :label-width="formLabelWidth" prop="orgStatus">
                <el-radio-group v-model="orgForm.orgStatus">
                    <el-radio label="0">启用</el-radio>
                    <el-radio label="1">禁用</el-radio>
                </el-radio-group>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button type="primary" size="small"  @click="saveOrgManage(orgForm)">保存</el-button>
            <el-button size="small" type="danger"  @click="resetForm('orgForm')">重置</el-button>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orgUpdate",
        props: ['isEdit', 'currentRow'],
        data(){
            return{
                rules: {
                    //表单校验
                    orgCode: [{required: true, message: "请输入机构代码", tigger: "change"}],
                    orgName: [{required: true, message: "请输入机构名", tigger: "change"}],
                   
                },
                orgForm: {
                    orgId: "", //id
                    orgCode: "", //code
                    orgName: "", //name
                    orgStatus: "",//状态
                    orgType:"",//类型
                    orgPid: ""//fatherId
                },
                formLabelWidth: "110px",
            }
        },
        created(){
            // if (this.isEdit){
                this.orgForm.orgId = this.currentRow.orgId;
                this.orgForm.orgCode = this.currentRow.orgCode;
                this.orgForm.orgName = this.currentRow.orgName;
                this.orgForm.orgStatus = this.currentRow.orgStatus;
                this.orgForm.orgType = this.currentRow.orgType;
                this.orgForm.orgPid = this.currentRow.orgPid;
            // }
        },
        methods: {
            saveOrgManage(orgForm) {
                this.$refs['orgForm'].validate((valid) => {
                    if (valid) {
                        //机构添加保存
                        let _this = this;
                        let orgUrl = "";
                        if(this.isEdit){
                             orgUrl = _this.API.updateOrganization;

                        }else{
                             orgUrl = _this.API.insertOrganization;  
                        }
                            _this.$http({
                            url: _this.$http.adornUrl(orgUrl),
                            method: 'post',
                            data:_this.$http.adornData(orgForm) 
                        }).then(({data}) => {
                            if (data.code === 0) {
                                _this.$message.success("操作成功");
                                this.$emit("updateCallback", true);
                            } else {
                                _this.$message.error("操作失败，请重新尝试");
                            }
                        }).catch(function (error) {
                        });
                    } else {
                        return false;
                    }
                });
            },
            //重置表单
            resetForm(refname) {
                //重置
                this.$nextTick(() => {
                    if (this.$refs[refname] !== undefined) {
                        this.$refs[refname].resetFields();
                    }
                });
            },
        }
    }
</script>

<style lang="scss" scoped>

</style>


